.navWrapper {
    width: 200px;
    right: 20px;
    height: 100%;
    display: flex;
    position: fixed;
    align-items: flex-end;
    flex-direction: column;
    justify-content: center;
    background: transparent;
    gap: 20px;
    overflow: hidden;
    z-index: 1;

    margin: auto;

 }

.navWrapper .NavBtns {
    width: 80px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    border: none;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    transition: all 0.35s ease;
}

.navWrapper .NavBtns::after {
    content: "";
    height: 80%;
    width: 100%;
    position: absolute;
    right: 0;
    border-radius: 50px 0 0 50px;
    z-index: 0;
    transition: background 0.3s ease;
}

.navWrapper .NavBtns:hover::after {
    background: linear-gradient(to right, rgb(29, 29, 29), transparent);
}

.navWrapper .NavBtns.linkWrapperAnim {
    width: 200px;
}

.navWrapper .NavBtns .paginationWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70%;
    height: 50px;
    position: relative;
    transition: all 0.3s ease-in-out;
    right: 0px;
}

.navWrapper .NavBtns .paginationWrapper::after {
    content: "";
    background-color: #fff;
    height: 1px;
    width: 70%;
    position: absolute;
    bottom: 0;
    right: -100%;
    z-index: 1;
}

.navWrapper .NavBtns .paginationWrapper:hover::after {
    right: 0;
}

.navWrapper .NavBtns .paginationWrapper .circle-linkPagination {
    width: 1px;
    height: 20px;
    border: 2px solid #353232;
    position: absolute;
    z-index: 2;
    right: 0;
}

.navWrapper .NavBtns .paginationWrapper .circle-linkPagination.active {
    border: none;
    background-color: rgb(161, 255, 10);
    box-shadow: -1px 0 5px limegreen;
}

.navWrapper .NavBtns .paginationWrapper .linkName {
    position: relative;
    width: 70%;
    right: -100%;
    white-space: nowrap;
    display: flex;
    align-items: center;
    height: 100%;
    transition: right 0.35s ease;
    color: #a0a0a0;
}

.navWrapper .NavBtns .paginationWrapper .linkName.h4TextAnim {
    right: 0;
    color: #fff;
    z-index: 2;
}