.Hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    position: relative;
    height: 100vh;
    width: 100%;

}

.Hero .socials {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;

    width: 80%;
    height: 60%;
    position: relative;
    position-try: relative;
    z-index: 1;
}

.Hero .socials .socialLinks {
    display: flex;
    height: auto;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin: 0px auto;

}

.Hero .socials .socialLinks .socialLink {
    gap: 10px;
    display: flex;
}

.mailBtn {
    display: flex;
    gap: 5px;
    text-decoration: none;
    padding: 5px;
    cursor: pointer;
    position: relative;
}

.mailBtn::after {
    height: 1px;
    width: 15%;
    display: flex;
    background-color: #fff;
    bottom: 0px;
    content: "";
    position: absolute;
    transition: all 350ms ease;
}

.mailBtn:hover::after {
    width: 100%;
}

.Hero .socials .hero-name-wrapper {
    text-transform: uppercase;
    display: flex;
    height: auto;
    width: 100%;
    gap: 5px;
    justify-content: center;
    align-items: center;
    margin: 0px auto;
    flex-direction: column;
}

.Hero .socials .hero-name-wrapper p {
    display: flex;
    margin: auto;
    color: #898989;
    letter-spacing: 10px;
    font-size: 12px;
}

.Hero .socials .hero-name-wrapper h1 {
    letter-spacing: 10px;
    display: flex;
    margin: auto;
    color: #fff;
    font-size: 50px;
}

.heroWriteUpContainer {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    top: 0px;
    /* background-color: red; */
    justify-content: center;


}

.heroWriteUpContainer .backgroundCirclesWrapper {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    position: relative;

    height: 100vh;
    width: 100%;

    /* overflow: hidden; */
}

.backgroundCircle {
    display: flex;
    position: absolute;
    margin: auto;
    background-color: transparent;
    border-radius: 50%;

}

.bckCircle1 {
    height: 120px;
    width: 120px;
    position: relative;
    justify-self: center;
}

.bckCircle1 img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.bckCircle2 {
    border: 1px solid #4d4d4d;
    height: 400px;
    width: 400px;
    opacity: .7;
}

.bckCircle3 {
    border: 1px solid #4d4d4d;
    height: 550px;
    width: 550px;
    opacity: .7;
}

.bckCircle4 {
    border: 1px solid #4d4d4d;
    height: 800px;
    width: 800px;
    opacity: .7;

}



.backgroundImageWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    height: 100px;
    width: 100px;
}

.backgroundImageWrapper img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}